export default {
  getVendorInvoices (state) {
    return state.invoices.sort((invoiceA, invoiceB) => {
      // most recent first
      return invoiceA.dateOrdered < invoiceB.dateOrdered
    })
  },
  getVendorInvoice (state) {
    return (invoiceId) => {
      return state.invoices.find((invoice) => {
        return invoice.id === invoiceId
      })
    }
  },
  getOrders (state) {
    return state.orders.sort((orderA, orderB) => {
      return orderA.dateOrdered > orderB.dateOrdered
    })
  },
  getUpdatedItems: state => state.items,
  getLikedItems: state => state.likedItems,
  getCartItems: state => state.cartItems,
  getVendorItems (state) {
    return vendorId => state.items.filter(item => {
      return item.vendorId === vendorId
    })
  },
  getItem (state) {
    return (itemId) => {
      return state.items.find((item) => {
        return item.id === itemId
      })
    }
  },
  getOverall (state) {
    return state.overall
  },
  getSales (state) {
    return state.sales
  },
  getLabels (state) {
    return state.labels
  },
  getVendor (state) {
    return state.vendor
  },
  getUser (state) {
    return state.user
  },
  getUsers (state) {
    return state.users
  },
  getLoading (state) {
    return state.loading
  },
  getError (state) {
    return state.error
  },
  getSuccess (state) {
    return state.success
  }
}



