<template>
  <v-container fluid>
    <!-- success alert -->
    <div
    class="mt-4"
    v-if="success">
      <app-success-alert 
      @dismissed="onDismissed"
      :text="success"
      class="white--text"></app-success-alert>
    </div>

    <!-- error alert row -->
    <div
    v-if="error"
    class="mb-2">
      <v-flex xs12>
        <app-error-alert 
        @dismissed="onDismissed"
        :text="error"
        class="black--text"></app-error-alert>
      </v-flex>
    </div>

    <v-layout row wrap
    class="mt-2">

      <v-flex xs6>
        <v-layout row wrap>
          <v-card-title>
            <v-flex xs12>
              <v-layout row wrap>
                <v-flex 
                xs12
                class="mb-3">
                  <h1 class="headline">Your Profile</h1>
                </v-flex>
              </v-layout>
            </v-flex>
            <v-flex xs12>
              <v-layout row wrap class="mb-4">
                <v-flex xs12 sm5>
                  <v-img
                  v-if="user.avatar === ''"
                  :src="require('@/assets/anonymous.png')"
                  height="250px"
                  contain>
                  </v-img>
                  <v-img
                    v-if="user.avatar !== ''"
                    :src="user.avatar"
                    height="250px"
                    contain
                  ></v-img>
                </v-flex>
                <v-flex xs12 sm7>
                  <v-card-title primary-title>
                    <div>
                      <div class="headline">Username</div>
                      <span>{{user.name}}</span>
                      <div class="headline">Email</div>
                      <span>{{user.email}}</span>
                      <div class="headline">Blk.Party user Since</div>
                      <span>{{user.dateJoined}}</span>
                      <div class="headline">Password</div>
                      <span>XXXXXX</span>
                    </div>
                  </v-card-title>
                </v-flex>
              </v-layout>
            </v-flex>
          </v-card-title>
        </v-layout>
      </v-flex>
      <v-flex xs6>
        <v-card>
          <v-card-title>
            <span class="headline ml-3">Your Past Orders</span>
          </v-card-title>
          <v-card-text 
          v-if="!ordersLoaded"
          class="ml-3">
            No orders yet. Feel free to get a few drinks!
          </v-card-text>
          <v-card-text v-if="ordersLoaded">
            <div>
              <v-list
              two-line>
                <v-list-tile
                  v-for="(pastOrder, key) in pastOrders"
                  :key="key"
                  avatar
                  class="my-3"
                >
                  <v-list-tile-avatar>
                    <img :src="pastOrder.imageURL">
                  </v-list-tile-avatar>
                  <v-layout row wrap>

                    <v-flex xs12 sm9>
                      <v-list-tile-content v-if="pastOrder.discount === '0'">
                        <v-list-tile-title class="title">
                          {{pastOrder.itemName}}
                        </v-list-tile-title>
                        <v-list-tile-sub-title
                        class="mt-2">
                          SGD {{pastOrder.priceSGD}} each
                        </v-list-tile-sub-title>
                      </v-list-tile-content>
                      <v-list-tile-content
                      v-if="pastOrder.discount !== '0'">
                        <v-list-tile-title class="title">
                          {{pastOrder.itemName}}
                        </v-list-tile-title>
                        <v-list-tile-sub-title
                        class="mt-2">
                          SGD <s>{{pastOrder.priceSGD}}</s> {{pastOrder.discountedPrice}} each
                          <span class="success--text font-weight-bold">{{pastOrder.discount}}% discount!</span>
                        </v-list-tile-sub-title>
                      </v-list-tile-content>
                    </v-flex>

                    <v-flex xs12 sm3>
                      <v-list-tile-action
                      justify-space-around
                      class="mt-1">
                        <v-layout row wrap>
                          <!-- shopping cart buttons -->
                          <v-tooltip
                          bottom>
                            <v-menu
                            :close-on-content-click="false"
                            v-if="!addedToCart(pastOrder.itemId)"
                            slot="activator"
                            >
                              <v-btn
                              slot="activator"
                              @click="setItemQty(1)"
                              icon>
                                <v-icon color="success">add_shopping_cart</v-icon>
                              </v-btn>
                              <v-card>
                                <v-card-text class="pa-4">
                                  <v-text-field
                                    color="success"
                                    v-model="qty"
                                    outline
                                    single-line
                                    type="number"
                                    :rules="rules.qty"
                                    suffix="unit(s)"
                                  >
                                    <v-tooltip
                                      slot="append-outer"
                                      style="top: -12px"
                                      bottom
                                    >
                                      <v-btn
                                      :disabled="!qtyValid"      
                                      slot="activator"
                                      flat
                                      icon
                                      @click="addToCart(pastOrder.itemId)">
                                        <v-icon
                                        color="success">
                                          check
                                        </v-icon>
                                      </v-btn>
                                      Submit Order
                                    </v-tooltip>
                                  </v-text-field>
                                </v-card-text>
                              </v-card>
                            </v-menu>
                            <span>Add to Cart</span>
                          </v-tooltip>
                          <v-tooltip
                          bottom>
                            <v-menu
                            :close-on-content-click="false"
                            v-if="addedToCart(pastOrder.itemId)"
                            slot="activator"
                            >
                              <v-btn
                              slot="activator"
                              @click="setItemQty(pastOrder.qty)"
                              icon>
                                <v-icon color="success">shopping_cart</v-icon>
                              </v-btn>
                              <v-card>
                                <v-card-text class="pa-4">
                                  <v-text-field
                                    color="success"
                                    v-model="qty"
                                    outline
                                    single-line
                                    type="number"
                                    :rules="rules.qty"
                                    suffix="unit(s)"
                                  >
                                    <v-tooltip
                                      slot="append-outer"
                                      style="top: -12px"
                                      bottom
                                    >
                                      <v-btn
                                      :disabled="!qtyValid"
                                      slot="activator"
                                      flat
                                      icon
                                      @click="addToCart(pastOrder.itemId)">
                                        <v-icon
                                        color="success">
                                          check
                                        </v-icon>
                                      </v-btn>
                                      Submit Order
                                    </v-tooltip>
                                    <v-tooltip
                                      slot="append-outer"
                                      style="top: -12px"
                                      bottom
                                    >
                                      <v-btn
                                      slot="activator"
                                      flat
                                      icon
                                      @click="removeFromCart(pastOrder.itemId)">
                                        <v-icon
                                        color="error">
                                          clear
                                        </v-icon>
                                      </v-btn>
                                      Remove All
                                    </v-tooltip>
                                  </v-text-field>
                                </v-card-text>
                              </v-card>
                            </v-menu>
                            <span>Adjust Item Qty</span>
                          </v-tooltip>
                          <p
                          v-if="addedToCart(pastOrder.itemId)"
                          >
                            x {{pastOrder.qty}}
                          </p>
                        </v-layout>
                      </v-list-tile-action>
                    </v-flex>
                  </v-layout>

                </v-list-tile>
              </v-list>
            </div>
          </v-card-text>

        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      qty: 1,
      rules: {
        qty: [val => val > 0 || 'Enter 1 or more in quantity please.']
      }
    }
  },
  mounted () {
    this.init()
  },
  computed: {
    ...mapGetters({
      items: 'getUpdatedItems',
      orders: 'getOrders',
      cartItems: 'getCartItems'
    }),
    user () {
      return this.$store.getters.getUser
    },
    ordersLoaded () {
      return this.pastOrders.length > 0
    },
    pastOrders () {
      let orderItems = []
      function getItemId (order) {
        var itemId = order.itemId
        return itemId
      }
      let itemIdArr = this.orders.map(getItemId)
      for (var i = 0; i < this.items.length; i++) {
        let itemId = this.items[i].itemId
        if (itemIdArr.indexOf(itemId) !== -1) {
          orderItems.push(this.items[i])
        }
      }
      return orderItems
    },
    // get error and success msgs
    error () {
      return this.$store.getters.getError
    },
    success () {
      return this.$store.getters.getSuccess
    },
    loading () {
      return this.$store.getters.getLoading
    }
  },
  methods: {
    init () {
      // load all available items
      this.$store.dispatch('itemsApi')
      if (this.user) {
        this.$store.dispatch('ordersApi', {userId: this.user.id})
      }
    },
    // dismiss alerts
    onDismissed () {
      this.$store.dispatch('clearError')
      this.$store.dispatch('clearSuccess')
    },
    addToCart (itemId) {
      if (this.user.id) {
        let payload = {
          userId: this.user.id,
          itemId: itemId,
          qty: this.qty
        }
        this.$store.dispatch('addItemToCart', payload)
        this.$store.dispatch('cartItemsApi', {userId: this.user.id})
        this.getItemQty(payload)
      } else {
        this.$store.dispatch('setSuccess', {message: 'Please login and/or register to add item to your shopping cart!'})
      }
    },
    removeFromCart (itemId) {
      if (this.user.id) {
        let payload = {
          userId: this.user.id,
          itemId: itemId
        }
        this.$store.dispatch('removeItemFromCart', payload)
        this.$store.dispatch('cartItemsApi', {userId: this.user.id})
      }
    },
    getItemQty (payload) {
      for (let i = 0; i < this.pastOrders.length; i++) {
        if (this.pastOrders[i].itemId === payload.itemId) {
          this.pastOrders[i].qty = payload.qty
          break
        }
      }
    },
    addedToCart (itemId) {
      if (this.cartItems === null || undefined) {
        return false
      } else {
        let added = false
        for (let i = 0; i < this.cartItems.length; i++) {
          this.getItemQty(this.cartItems[i])
          if (this.cartItems[i].itemId === itemId) {
            added = true
            break
          }
        }
        return added
      }
    },
    qtyValid () {
      if (this.qty > 0) {
        return true
      } else {
        return false
      }
    },
    setItemQty (itemQty) {
      this.qty = itemQty
    }
  }
}
</script>





