<script>
import LineChart from '@/components/lineChart'

export default {
  components: {
    LineChart
  },
  // vuex actions to retrieve overview data
  mounted () {
    // get latest items
    this.$store.dispatch('itemsApi')
    this.$store.dispatch('chartApi', this.vendor)
    this.$store.dispatch('vendorInvoicesApi', this.vendor)
    // load all liked items
    this.$store.dispatch('likedItemsApi', {userId: this.vendor.id})
    // load all cart items
    this.$store.dispatch('cartItemsApi', {userId: this.vendor.id})
  },
  computed: {
    vendor () {
      return this.$store.getters.getVendor
    },
    overall () {
      return this.$store.getters.getOverall
    },
    positiveChange () {
      return this.overall.change >= 0
    },
    invoices () {
      return this.$store.getters.getVendorInvoices
    },
    sales () {
      return this.$store.getters.getSales
    },
    labels () {
      return this.$store.getters.getLabels
    },
    vendorItems () {
      return this.$store.getters.getVendorItems(this.vendor.id)
    },
    // check if all required fields are filled
    addItemFormIsValid () {
      return this.files.length !== 0
    },
    filesLoaded () {
      return this.files.length !== 0
    },
    discountAmt () {
      if (this.discount === 0) {
        return 0
      } else {
        return ((this.discount / 100) * this.price).toFixed(2)
      }
    },
    discountedPrice () {
      if (this.discount === 0) {
        return this.price
      } else {
        return (this.price - this.discountAmt).toFixed(2)
      }
    },
    itemsLoaded () {
      return this.vendorItems.length !== 0
    },
    // get error and success msgs
    error () {
      return this.$store.getters.getError
    },
    success () {
      return this.$store.getters.getSuccess
    },
    loading () {
      return this.$store.getters.getLoading
    },
    detailsFormIsValid () {
      return (
        this.itemName &&
        this.price &&
        this.discount &&
        this.description &&
        this.itemType &&
        this.deliveryTimeDays <= 3 &&
        this.deliveryTimeHrs <= 23 &&
        this.checkTerms
      )
    },
    updateFormIsValid () {
      return (
        this.itemName &&
        this.price &&
        this.discount &&
        this.description &&
        this.itemType &&
        this.deliveryTimeDays <= 3 &&
        this.deliveryTimeHrs <= 23 &&
        this.checkTerms
      )
    },
    imageUpdated () {
      return this.updatedFile.length !== 0
    }
  },
  methods: {
    showOverview () {
      // clear all error and success msgs
      this.onDismissed()
      // reload chart, invoices and overall apis
      this.$store.dispatch('chartApi', this.vendor)
      this.$store.dispatch('vendorInvoicesApi', this.vendor)
      // set side view component visibility
      this.overview = true
      this.profile = false
      this.addItem = false
      this.updateItem = false
    },
    showProfile () {
      // clear all error and success msgs
      this.onDismissed()
      // set side view component visibility
      this.overview = false
      this.profile = true
      this.addItem = false
      this.updateItem = false
    },
    showAddItem () {
      // clear all error and success msgs
      this.onDismissed()
      // set side view component visibility
      this.overview = false
      this.profile = false
      this.addItem = true
      this.updateItem = false
    },
    showUpdateItem () {
      // clear all error and success msgs
      this.onDismissed()
      // get latest items
      this.$store.dispatch('itemsApi')
      // set side view component visibility
      this.overview = false
      this.profile = false
      this.addItem = false
      this.updateItem = true
    },
    // addItem detailsForm actions
    // retrieve file details, if any, and render details text fields
    addDetails (index) {
      if (this.files[index].itemName !== null || undefined || '') {
        // set fileIndex to current image index in array
        this.fileIndex = index
        // define detailsForm params
        this.itemName = this.files[index].itemName
        this.price = this.files[index].price
        this.description = this.files[index].description
        this.itemType = this.files[index].itemType
        this.discount = this.files[index].discount
        this.deliveryTimeDays = this.files[index].deliveryTimeDays
        this.deliveryTimeHrs = this.files[index].deliveryTimeHrs
        this.checkTerms = this.files[index].checkTerms
      } else {
        this.resetDetailsForm()
      }
      // show detailsForm
      this.viewDetails = true
    },
    resetDetailsForm () {
      this.itemName = ''
      this.price = 0
      this.description = ''
      this.itemType = ''
      this.discount = 0
      this.deliveryTimeDays = 0
      this.deliveryTimeHrs = 1
      this.checkTerms = false
    },
    closeDetailsForm () {
      // reset detailsForm
      this.resetDetailsForm()
      // close viewDetails section
      this.viewDetails = false
    },
    submitDetailsForm () {
      // set file details
      this.files[this.fileIndex].itemName = this.itemName
      this.files[this.fileIndex].price = this.price
      this.files[this.fileIndex].description = this.description
      this.files[this.fileIndex].itemType = this.itemType
      this.files[this.fileIndex].discount = this.discount
      this.files[this.fileIndex].discountAmt = this.discountAmt
      this.files[this.fileIndex].discountedPrice = this.discountedPrice
      this.files[this.fileIndex].deliveryTimeDays = this.deliveryTimeDays
      this.files[this.fileIndex].deliveryTimeHrs = this.deliveryTimeHrs
      this.files[this.fileIndex].checkTerms = this.checkTerms
      // reset addDetails fields
      this.closeDetailsForm()
      // send success alert
      this.$store.dispatch('setSuccess', {message: 'Successfully added ' + this.files[this.fileIndex].itemName + ' details!'})
    },
    // reset addItem form
    resetAddItemForm () {
      this.files = []
    },
    // store selected files in an array
    filesSelected (uploadedFiles) {
      // push files to files array
      for (var i = 0; i < uploadedFiles.length; i++) {
        let filename = uploadedFiles[i].name
        // check for file extension
        if (filename.lastIndexOf('.') <= 0) {
          return this.$store.dispatch('setError', {
            message: 'Please select a valid ' + uploadedFiles[i].name + ' file'
          })
        }
        this.files.push(uploadedFiles[i])
      }
      // generate previews for files
      this.getPreviews()
      // send alert to add item details
      this.$store.dispatch('setSuccess', {
        message: uploadedFiles.length + ' file(s) selected. Please add item details before submitting.'
      })
    },
    getPreviews () {
      // Iterate over all of the files and generate an image preview for each one.
      for (let i = 0; i < this.files.length; i++) {
        // Create a new FileReader object
        const reader = new FileReader()

        // Add an event listener to update src on file preview when the file has been loaded
        reader.addEventListener('load', () => {
          this.$refs['image' + parseInt(i)][0].src = reader.result
        })

        // Read file data through reader and listen to event propagated to set image src to reader result.
        reader.readAsDataURL(this.files[i])
      }
    },
    // remove one file from file array
    removeImage (index) {
      if (index !== -1) {
        this.files.splice(index, 1)
        // close details form
        this.viewDetails = false
      }
      this.getPreviews()
    },
    submitAddItemForm () {
      // set initial ready boolean
      let allReady = true
      // send error alert for any file with undefined itemName
      for (let i = 0; i < this.files.length; i++) {
        if (this.files[i].itemName === undefined) {
          allReady = false
          this.$store.dispatch('setError', {
            message: 'Please enter item details for ' + this.files[i].name + ' before submitting all items'
          })
        }
      }
      // iterate over all files to upload if allReady remains true
      if (allReady) {
        this.fileCount = this.files.length
        for (let j = 0; j < this.files.length; j++) {
          this.upload(this.files[j])
        }
        this.resetAddItemForm()
      }
    },
    upload (file) {
      const payload = {
        file: file,
        itemName: file.itemName,
        priceSGD: file.price,
        description: file.description,
        itemType: file.itemType,
        discount: file.discount,
        discountAmt: file.discountAmt,
        discountedPrice: file.discountedPrice,
        deliveryTimeDays: file.deliveryTimeDays,
        deliveryTimeHrs: file.deliveryTimeHrs,
        vendorId: this.vendor.id,
        createDate: new Date()
      }
      // dispatch payload to vuex action
      this.$store.dispatch('uploadFile', payload)
    },
    // vendorItem dialog methods
    openDialog (vendorItem) {
      // set dialog form params
      this.imageURL = vendorItem.imageURL
      this.itemName = vendorItem.itemName
      this.price = vendorItem.priceSGD
      this.description = vendorItem.description
      this.itemType = vendorItem.itemType
      this.discount = vendorItem.discount
      this.deliveryTimeDays = vendorItem.deliveryTimeDays
      this.deliveryTimeHrs = vendorItem.deliveryTimeHrs
      this.itemId = vendorItem.itemId
      this.createDate = vendorItem.createDate
      // open itemDialog
      this.vendorItemDialog = true
    },
    cancelUpdate () {
      // reset updatedImage to empty
      this.updatedFile = []
      // close itemDialog
      this.vendorItemDialog = false
    },
    // update item image
    updateFileSelected (file) {
      // push file to updatedFile
      for (var i = 0; i < file.length; i++) {
        let filename = file[i].name
        // check for file extension
        if (filename.lastIndexOf('.') <= 0) {
          return this.$store.dispatch('setError', {
            message: 'Please select a valid ' + filename + ' file'
          })
        }
        this.updatedFile.push(file[i])
      }
      // generate previews for files
      this.getUpdatePreview()
    },
    getUpdatePreview () {
      // Iterate over all of the files and generate an image preview for each one.
      for (let i = 0; i < this.updatedFile.length; i++) {
        // Generate an image preview for updated file.
        const reader = new FileReader()

        // Add an event listener to update src on file preview when the file has been loaded
        reader.addEventListener('load', () => {
          this.$refs['updatedImage'].src = reader.result
        })
        // Read file data through reader and listen to event propagated to set    image src to reader result.
        reader.readAsDataURL(this.updatedFile[i])
      }
    },
    // submit item updates
    submitUpdateForm () {
      const payload = {
        itemId: this.itemId,
        itemName: this.itemName,
        priceSGD: this.price,
        description: this.description,
        itemType: this.itemType,
        discount: this.discount,
        discountAmt: this.discountAmt,
        discountedPrice: this.discountedPrice,
        deliveryTimeDays: this.deliveryTimeDays,
        deliveryTimeHrs: this.deliveryTimeHrs,
        vendorId: this.vendor.id,
        createDate: this.createDate,
        updateDate: new Date()
      }
      if (this.updatedFile.length !== 0) {
        payload.updatedFile = this.updatedFile[0]
      } else {
        payload.updatedFile = null
        payload.imageURL = this.imageURL
      }
      // dispatch payload to vuex action
      this.$store.dispatch('updateFile', payload)
      // get latest items
      this.$store.dispatch('itemsApi')
    },
    // open confirm delete item dialog
    removeItem (vendorItem) {
      // set dialog params
      this.itemId = vendorItem.itemId
      this.imageURL = vendorItem.imageURL
      this.itemName = vendorItem.itemName
      this.price = vendorItem.priceSGD
      // open deleteItemDialog
      this.deleteItemDialog = true
    },
    // delete item after confirmation
    deleteItem () {
      // delete item from firebase
      this.$store.dispatch('deleteItem', {
        itemId: this.itemId
      })
      // get latest items
      this.$store.dispatch('itemsApi')
      // close delete item dialog
      this.deleteItemDialog = false
    },
    onDismissed () {
      this.$store.dispatch('clearError')
      this.$store.dispatch('clearSuccess')
    }
  },
  data () {
    return {
      drawer: true,
      // overview data
      period: 'Last Month',
      // datatable headers
      headers: [
        {
          text: 'Invoice ID',
          value: 'invoiceId'
        },
        {
          text: 'Item Name',
          value: 'itemName'
        },
        {
          text: 'Quantity',
          value: 'quantity'
        },
        {
          text: 'Client First Name',
          value: 'firstName'
        },
        {
          text: 'Client Last Name',
          value: 'lastName'
        },
        {
          text: 'Delivery Address',
          value: 'destination'
        },
        {
          text: 'Zip Code',
          value: 'zipCode'
        },
        {
          text: 'Client Phone No.',
          value: 'phoneNumber'
        },
        {
          text: 'Client Email',
          value: 'deliveryEmail'
        },
        {
          text: 'Date Invoiced',
          value: 'dateOrdered'
        },
        {
          text: 'Time Invoiced',
          value: 'timeOrdered'
        },
        {
          text: 'Expected Date',
          value: 'expectedDate'
        },
        {
          text: 'Expected Time',
          value: 'expectedTime'
        },
        {
          text: 'Date Delivered',
          value: 'dateDelivered'
        },
        {
          text: 'Time Delivered',
          value: 'timeDelivered'
        },
        {
          text: 'Retail Price (SGD)',
          value: 'priceSGD' },
        {
          text: 'Amount Collected (SGD)',
          value: 'amountPaid'
        },
        {
          text: 'Discount Applied (SGD)',
          value: 'discountAmt'
        },
        {
          text: 'Blk.Party Fee (SGD)',
          value: 'fee'
        },
        {
          text: 'Total Sales (SGD, net)',
          value: 'netSales'
        }
      ],
      // sideView toggles
      fab: false,
      overview: true,
      profile: false,
      addItem: false,
      updateItem: false,
      // addItem data
      files: [],
      fileIndex: '',
      fileCount: 0,
      // addDetails data
      itemName: '',
      price: 0,
      description: '',
      itemType: '',
      discount: 0,
      deliveryTimeDays: 0,
      deliveryTimeHrs: 1,
      checkTerms: false,
      showTerms: false,
      showConditions: false,
      viewDetails: false,
      rules: {
        deliveryTimeDays: [val => val < 4 || 'Max. 3 days delivery'],
        deliveryTimeHrs: [val => val < 24 || 'Max 23 hrs delivery'],
        price: [val => val > 0 || 'This field is required. Enter a number higher than zero please.'],
        discount: [val => val > -1 || 'Enter a number equal to or higher than zero please.'],
        name: [val => (val || '').length > 0 || 'This field is required']
      },
      types: [
        'Beer', 'Whisky', 'Vodka', 'Gin', 'Rum', 'Tequila', 'Brandy', 'Wine', 'Champagne'
      ],
      // edit items data
      vendorItemDialog: false,
      updatedFile: [],
      itemId: null,
      createDate: null,
      imageURL: '',
      // delete items data
      deleteItemDialog: false,
      year: 2018,
      terms: [
        'Once the Seller has been admitted to sell on BLK. PARTY, it shall be provided with a wide range of services, which shall be adapted at regular intervals in line with the product range and seasonal influences.',
        'BLK. PARTY provides sellers with sole discretion in uploading artwork, photos, prices, alcohol content and delivery times on BLK. PARTYs website. BLK. PARTY shall process orders through the BLK. PARTY site and provide invoices to both Customer(s) and the Seller in addition to keeping a copy for internal operations. All transactions will be cash-on-delivery. To this end, Seller shall have sole responsibility to fulfil order(s)  according to expected delivery date(s) and time(s) of each Seller product and collect all monies in exchange for product(s) and services.',
        'Seller shall be solely responsible for providing accurate and complete advertising material and information including but not limited to text, images, alcohol content, and all other information related to Seller product(s) registered with BLK. PARTY. The Seller furthermore indemnifies that they have the legal right to use any and all such advertising materials. Subject to BLK. PARTYs rights under this agreement or otherwise, the Seller shall be free to decide whether and how long to place the BLK. PARTY advertising materials within the duration of Seller subscription, unless otherwise required by BLK. PARTY. The Seller shall be entitled to remove the advertising materials at any time during the fore-mentioned period. BLK. PARTY is entitled to remove any and all advertising materials at any time without prior approval from the Seller for any reason.  The Seller is only allowed to place BLK. PARTY advertising materials on advertising media that has been registered with and approved by BLK. PARTY.',
        'The Seller shall to the best of their capacity, as per regular commercial proceedings, fulfil all orders placed by their customers through BLK. PARTYs network. Failure of this might result in Blk. Party fulfilling the Sellers order(s) and consequentially a penalty charge equaling to three times the value of the order(s) in Seller subscription credit.',
        'BLK. PARTY is indemnified by The Seller for any and all legal action arising from The Sellers failure to fulfil such orders and obligations to the customers. This indemnity extends to the event in which litigation arises from such failures to fulfil orders or obligations to customers. In such an event, The Seller shall be solely liable, even after the conclusion of this Agreement, and The Sellers commercial relationship with BLK. PARTY has concluded, for legal, and potentially criminal legal action taken by the customer or their representatives.',
        'BLK. PARTY reserves the right to any and all information related to a failure on the part of the seller to fulfil its obligation(s) to the customer held by The Seller. This includes correspondence between The Seller and the customer, invoices, proofs of transaction and furthermore, BLK. PARTY reserves the right to investigate such a failure to fulfil orders by The Seller on behalf of the customer. BLK. PARTY shall, in the event that such an investigation leads to incomplete or untenable results, or whereas an agreement cannot be reached between The Seller and the customer to remedy the issue, refer the customer directly to The Seller.',
        'In exchange for access to BLK. PARTYs service(s), including but not limited to the placement of listings for products on BLK. PARTYs Product, the Seller shall adhere to BLK. PARTYs credit subscription fee(s), value of which shall depend on the subscription program chosen upon registration.',
        'BLK. PARTY shall not establish any other contractual relationship between the Parties that goes beyond this Agreement.',
        'BLK. PARTY shall operate its website and the services offered thereon, such as the provision of product feed, within the limits of the technical capacities available to BLK. PARTY. BLK. PARTY shall not be obliged, within these limits, to provide error-free and interruption-free availability of the website. The quality and correctness of the products, advertising material and csv files offered on the BLK. PARTYs Seller platform shall fall within the exclusive discretion of BLK. PARTY.',
        'All activities of the Seller shall be logged via the platform tracking system and made accessible to the Seller via the platform statistics and reports. The commission that the Seller pays to BLK. PARTY shall be based on the quantity of Seller products sold and not the resulting net shopping basket value.',
        'The Seller shall, in the context of its participation in the platform and in accordance with the terms and conditions that the Seller agreed with BLK. PARTY in this respect, not be entitled to receive a commission from BLK. PARTY in relation to net transactions that are generated, by its active promotion of BLK. PARTY on the Sellers Website/App, thereafter if the action of using the Advertising materials is leading to a net transaction and it is the last paid marketing advertorial the end-user is using.',
        'In the event of an ordinarily negligent breach of an obligation which is material to the achievement of the contractual purpose (material contractual obligation), the liability of BLK. PARTY shall not exceed the total of the credit commissions paid or payable by the Seller under this Agreement in the transaction immediately prior to when the event giving rise to the most recent claim of liability occurred.',
        'The aforementioned limitation of liability shall also apply to the personal liability of BLK. PARTY employees, representatives and executive bodies.',
        'No further liability on the part of BLK. PARTY shall exist.',
        'BLK. PARTY and the Seller agree that tracking and reporting are being carried out by BLK. PARTYs Seller Platform by which a cookie is dropped on the users browser to track transaction back to each specific Seller and by which the device ID is used to track transactions generated from BLK. PARTY app.',
        'Cookie will last for duration of subscription and be disabled at the sole discretion of BLK. PARTY.',
        'BLK. PARTY and the Seller are independent contractors, and nothing in this agreement will create any partnership, joint venture, agency, franchise or sales representative relationship between the Parties. The Seller will have no authority to make or accept any offers or representations on behalf of BLK. PARTY.',
        'The Seller shall not subcontract any of its rights or obligations under this agreement without prior written consent of BLK. PARTY.',
        'The Seller may transfer claims against BLK. PARTY based on this Agreement to third parties only with BLK. PARTYs written consent.',
        'Neither of the Parties to the contract can have the right to transfer, assign or subcontract all or part of its rights or obligations deriving from this agreement without the written consent of the other Party. In case having a consent, all term and conditions of this Agreement will be kept unchanged except for having mutual consent.',
        'Either Party to the contract may set off or exercise a right of retention only in relation to the other Partys receivables that are uncontested or have become res judicata.'
      ],
      conditions: [
        'The Seller shall be expressly prohibited from using and/or modifying the Advertising Materials and content accessed via the BLK. PARTYs Seller Platform other than as expressly allowed under the terms of this Contract without BLK. PARTYs prior written agreement including email.',
        'The Seller shall not, without prior written consent by BLK. PARTY, be allowed to use advertising in any form including e-mails to promote BLK. PARTY without prior written approval and vetting by BLK. PARTY.',
        'BLK. PARTY will be free of all third-party requirements in case of issues because of the mailing Seller. The Seller guarantees that they take responsibility in case of complaints concerning the e-mail. The Seller is not allowed to use the brand BLK. PARTY within the e-mail address, within the URL, within the source code, and within the subject of the e-mail. The Seller has to make sure that it is clear that the e-mail comes from a Seller and not from BLK. PARTY directly. The e-mail has to be approved by BLK. PARTY before it will be sent. The Seller has to compensate the costs in case of breach of third party requirements or breach of the above restrictions.',
        'BLK. PARTY will be free of all third-party requirements in case of issues because of the mailing Seller. The Seller guarantees that they take responsibility in case of complaints concerning the e-mail. The Seller is not allowed to use the brand BLK. PARTY within the e-mail address, within the URL, within the source code, and within the subject of the e-mail. The Seller has to make sure that it is clear that the e-mail comes from a Seller and not from BLK. PARTY directly. The e-mail has to be approved by BLK. PARTY before it will be sent. The Seller has to compensate the costs in case of breach of third party requirements or breach of the above restrictions.',
        'The Seller shall be responsible for the content of the advertising media, content and information in any form provided to BLK. PARTY or other relevant Seller media and shall, for the term of this Agreement, provide no content to BLK. PARTY or other relevant advertising media that breaches applicable law, public morals or third-party rights. Prohibitions shall include, but not be limited to, representations that glorify violence, sexual and pornographic content and illustrations, misleading statements or discriminatory content (e.g. in respect of gender, race, politics, religion, nationality or disability). Such content may neither be mentioned any content, information or other relevant advertising media including SELLERs media, nor may links be created from the BLK. PARTYs Product or other relevant advertising media to corresponding content on other websites.',
        'Further thereto, BLK. PARTY reserves sole discretion to deny usage of any content or information on BLK. PARTY, deemed to be in breach of these conditions, to terminate contractual relations with The Seller without prior notification immediately, in such an event, and to remove any and all Sellers product and content on BLK. PARTY product and related media, extending in addition to related media advertising, social media and blog posts, without obligation to refund payments made by The Seller for any of the abovementioned services and products provided by BLK. PARTY.',
        'The Sellers advertising media shall not conduct, undertake, use, perform or exercise deal, torrent or streaming activities without BLK. PARTYs prior consent.',
        'The Seller shall be prohibited from creating and/or maintaining websites/apps and/or Sellers advertising media in any form that might lead to risk of confusion with the web/mobile presence of BLK. PARTY. The Seller shall neither be allowed to mirror said presence nor to copy graphics, texts or other content from BLK. PARTY website. It is strictly prohibited to crawl any of BLK. PARTYs webpages. In particular, the Seller shall avoid creating the impression whether publically or privately that the Sellers Website is a project of BLK. PARTY or that its operator is economically linked to BLK. PARTY in any way or any other relationship or affiliation between the Seller and BLK. PARTY that goes beyond this agreement. Any use, by the Seller, of materials or content from BLK. PARTY web presence or its logos or brands shall require BLK. PARTY prior written approval.',
        'The Seller shall be liable, vis--vis BLK. PARTY, for ensuring that its advertising content are neither in direct nor in indirect breach of domestic or foreign third-party property rights or other rights that do not meet any special statutory protection.',
        'It is strictly prohibited to drive search-engine-marketing and other keyword-based advertising traffic using the BLK. PARTY brand or private labels, to market BLK. PARTYs product. In other words, "BLK. PARTY", Blk. Party, blk. party, Block Party and other similar words which could be misleading as BLK. PARTY must be entered as a negative keyword.',
        'Advertising BLK. PARTY through social media activities (including but not limited to Facebook, Instagram, YouTube, Pinterest, Twitter) is granted upon request and should not include any trademarks of BLK. PARTY, or display misleading content (i.e. that may look like official BLK. PARTY social media activities). No social media activities including but not limited to sharing of posts through all social media platforms and creation of BLK. PARTY fan pages shall be granted only upon approval from Blk. Party.',
        'The Seller shall not use BLK. PARTYS product, name, logos and trademarks to set up campaigns on third party Seller networks. The Seller is only allowed to direct its own traffic and/or its own sub Seller traffic in case of networks, to the BLK. PARTYs product.',
        'Failure on the part of The Seller to adhere to the terms set out above shall constitute an immediate breach of this Agreement. BLK. PARTY reserves the right to remove any and all of The Sellers products and content from BLK. PARTY product and media, to permanently ban The Seller from BLK. PARTY services as a whole, to take legal action against The Seller where an actionable claim exists, and to report The Seller to the Police or other authorities, in the event that BLK. PARTY finds The Seller in breach of criminal law.',
        'The Seller shall warrant that it will set cookies only if advertising material made available by the BLK. PARTY Seller Program is in visible use on the Sellers Website and the user clicks voluntarily and consciously. The use of layers, add-ons, iFrames, pop-up, pop-under, site-under, auto-redirect advertisements which automatically redirect the user to advertiser websites without the users engagement or action (e.g. click, touch), cookie dropping, postview technology, misleading advertisements that result in misleading clicks that display expected content, shall not be permitted and are strictly prohibited. In particular for Apps campaigns, advertisements that result in forced installations of advertiser applications. For clarification purposes, forced-installation also includes the act of not asking users for permission before initiating a download/redirect.',
        'The use of offers, creative or brand names for any case of competition or lottery is strictly prohibited.',
        'The Seller must seek written approval from Blk. Party to create and promote any vouchers and/or additional promotion activities. Additional SELLER vouchers and/or additional promotional activities is developed and implemented solely upon the discretion of BLK. PARTY and will be subjected to additional charges unless otherwise stated in writing. SELLERS may not promote vouchers that BLK. PARTY has approved explicitly for Sellers or communicated by means of Seller MEDIA including but not limited to emails and newsletters. The promotion of vouchers and/or additional promotional activities related to any SELLER products listed on BLK. PARTY Product, including but not limited to end customer newsletters, print advertisements or customer service contacts, shall not be permitted and strictly prohibited.',
        'Any breach, by the Seller, of its obligations stipulated in this Agreement or any other industrial property rights or copyrights of BLK. PARTY shall entitle BLK. PARTY to terminate this Agreement for good cause in accordance with the statutory provisions, without refunding the credit subscription fee paid by The Seller to BLK. PARTY. This shall not affect any additional claims against the Seller to which BLK. PARTY is entitled, including but not limited to notifying the police of suspected criminal activity of The Seller in dealing with BLK. PARTY, including through use of BLK. PARTY Products and services to interact with customers in an illegal manner, collection of any and all debts owed to BLK. PARTY and the right to investigate The Seller on behalf of the customer, and the obligation of The Seller to conduct an internal investigation on suspicion of fraudulent activities or breach of contract, even after termination of this Agreement. In particular, BLK. PARTY shall be entitled, vis--vis the Seller, to withhold or cease all and any services related to said Seller.',
        'BLK. PARTY shall remove Seller advertising material without delay from its products and media if The Seller requests it to do so. In such an event, seller slots will remain available to The Seller until the end of the active subscription period.',
        'If BLK. PARTY is sued by third parties on account of the Sellers breach of contractual obligations or on account of the Seller violation of a statutory provision in relation to the placement of BLK. PARTY advertising material, the Seller shall be contractually obliged to indemnify BLK. PARTY against all third-party claims that are asserted on account of the aforementioned breaches. If, for its legal defense, BLK. PARTY requires the Seller to provide information or explanations, the Seller shall be obliged to make the same available to BLK. PARTY within necessary period no later than three (03) days and also to provide reasonable support to BLK. PARTY in its legal defense.',
        'In addition, the Seller shall compensate BLK. PARTY for any costs resulting from a claim by third parties on account of the infringement of the aforementioned rights and/or obligations; such costs shall, for example, include lawyers fees, court or other dispute resolution costs, particularly costs of independent proceedings for taking evidence, damages and other disadvantages that BLK. PARTY suffers thereby.',
        'The Seller shall not purchase any Product(s) through his/her own Seller promotions. Also, the Seller shall not cause any third parties to use the Seller Program to purchase any Product(s) with the intention of reselling such product or for commercial use of any kind. Transactions are not eligible where the Seller or sub-Seller is simultaneously owning or managing the Seller account (whether directly or not). For the avoidance of doubt, such transactions shall be deemed as being brought about through collusion and considered an invalid transaction.',
        'The Seller covenants that it has and will maintain all licenses, permits, approvals, registrations or the like, to perform the matters contemplated under this Agreement and that it shall carry out this Agreement in compliance with relevant law of Singapore, particularly any local advertising laws, its guiding legislation and legal provisions on data privacy, including but not limited to the Personal Data Protection Act 2012.',
        'In the event of a breach, (including but not limited to the use of non- permitted traffic and sources, fraudulent actions or the violation of the terms stated in this Agreement), BLK. PARTY reserves the right to deem as chargeback: (i) any pending payment owed to the Seller, (ii) the total amount of the payout for the period when the breach was found, (iii) any future payout earned by the Seller proven to have originated from the breach or violation.',
        'In the case of a violation originated by an identified Seller network sub Seller an additional chargeback can be applied equivalent to 50% of the sub Seller payout.',
        'In such an event, The Seller shall be obliged to fulfil all orders processed and submitted to them by BLK. PARTY in the agreed upon time frame, for the agreed upon price, as listed on BLK. PARTYs website. In addition, The Seller shall be obliged to inform BLK. PARTY about any sudden changes or of an inability on the part of The Seller for fulfil its obligations to the customer, due to, for example, product recalls of any kind, within three working days. In addition, The Seller shall be obliged to refund all money to customers, for orders unfulfilled as such.'
      ]
    }
  }
}
