<style scoped>
  /* style for file dropbox */
  .dropbox {
    border: 2px dashed grey;
    background: lightcyan;
    color: dimgray;
    padding: 10px 10px;
    min-height: 300px; 
    position: relative;
    cursor: pointer;
  }

  .dropbox:hover {
    background: lightblue; 
  }

  .dropbox p {
    font-size: 1.2em;
    text-align: center;
    padding: 50px 0;
  }
  /* hide input field */

  .input_file {
    opacity: 0;
    width: 100%;
    height: 300px;
    position: absolute;
    cursor: pointer;
  }

  .input_updateFile {
    opacity: 0; 
    top: 50%;
    width: 100%;
    height: 30px;
    position: absolute;
    cursor: pointer;
  }
  /* style for netflix like gallery scroll */
  .gallery-row > .tile-scroll{
    overflow-x: auto;
    white-space: nowrap;
  }

  .gallery-row > .tile-scroll > .tile {
    display: inline-block;
    float: none;
  }

  .tile {
    padding-bottom: 0px;
    padding-top: 0px;
    display: block;
    width: 45%;
    height: auto;
    position: relative;
  }

  .tile__img {
    object-fit: cover;
    width: 100%;
    height: 300px;
  }

  .updateTile__img {
    object-fit: cover;
    width: 100%;
    height: 500px;
  }
  /* style for gallery tile overlay*/
  .overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #545454 !important;
    overflow: hidden;
    width: 100%;
    height: 0;
    transition: height 0.5s ease;
    opacity: .85;
  }

  .tile:hover .overlay {
    height: 100%;
  }

  .tile:hover {
    transition: all .2s ease-in-out;
    transform: scale(1.02); 
    position:relative;
    transition-timing-function:cubic-bezier(0.5, 0, 0.1, 1);
    transition-duration: 400ms;
  }
  /* style for texts in overlay */
  .remove {
    white-space: nowrap;
    color: #FFFFF8;
    font-size: 20px;
    position: absolute;
    overflow: hidden;
    top: 50%;
    left: 33%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
  }

  .remove:hover {
    cursor: pointer;    
    color: #E64F64;
  }

  .details {
    white-space: nowrap;
    color: #FFFFF8;
    font-size: 20px;
    position: absolute;
    overflow: hidden;
    top: 50%;
    left: 66%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    cursor: pointer;    
  }

  .changeImage {
    white-space: nowrap;
    color: #FFFFF8;
    font-size: 20px;
    position: absolute;
    overflow: hidden;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    cursor: pointer;    
  }
  
  .sideView {
    overflow-y: scroll;
  }

</style>




