                    <v-flex xs12>
                      <v-text-field
                      name="retype-password"
                      label="Retype Password"
                      v-model="retypePassword"
                      type="password"
                      color="blue" 
                      required></v-text-field>
                    </v-flex>
                  </v-layout>
                  <v-layout row wrap>
                    <v-flex xs12>
                      <v-checkbox
                        v-model="age"
                        color="success"
                        :rules="[rules.required]"
                      >
                        <div slot="label">
                          I declare that I am 18 year-old and above.
                        </div>
                      </v-checkbox>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs12>
                      <v-btn 
                      class="blue"
                      dark
                      :disabled="loading"
                      :loading="loading"
                      @click="onUserRegister"
                      >Register
                      <span slot="loader" class="custom-loader">
                        <v-icon light>cached</v-icon>
                      </span>
                      </v-btn>
                      <v-btn
                      flat small color="blue"
                      @click="switchUserLogin">
                        Already a Blk.Party user? Login here
                      </v-btn>
                    </v-flex>
                  </v-layout>
                </v-flex>
              </v-container>
            </v-card-text>
          </v-card>
        </v-menu>
        <v-btn
        v-if="loggedIn"
        @click="onLogout"
        :to="'/'"        
        flat>
          <v-icon left>exit_to_app</v-icon>
          Logout
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <v-content class="mt-5 pt-5">
      <router-view/>
    </v-content>
    <v-footer class="ml-3 transparent" app>
      <span>&copy; Blk.Party 2018</span>
    </v-footer>
    <!-- login age confirmation dialog -->
    <v-dialog
    transition="dialog-bottom-transition"
    persistent
    width="400px"
    v-model="loginDialog">
      <v-card>
        <v-card-title
          class="headline warning"
          primary-title
        >
          Are you sure you are 18-year-old and above?
        </v-card-title>
        <v-card-text>
          And no, Mom, this is not what you think it is... (in case Mom is spying)
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-flex xs6
          justify-center
          class="ml-2 mr-1">
            <h3>I confess, I will return when I'm 18-year-old</h3>
            <v-btn
              color="grey"
              flat
              @click="illegalAge()"
            >
              Exit
            </v-btn>
          </v-flex>
          <v-flex xs6
          justify-center
          class="mr-2 ml-1">
            <h3>Yes, I am old enough to have a Blk.Party</h3>
            <v-btn
              color="success"
              flat
              @click="legalAge()"
            >
              Continue
            </v-btn>
          </v-flex>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      drawer: false,
      query: '',
      // login/register params
      name: '',
      avatar: '',
      email: '',
      password: '',
      retypePassword: '',
      showPassword: false,
      loginStatus: 'Login',
      userLogin: true,
      userRegister: false,
      vendorLogin: false,
      vendorRegister: false,
      loader: null,
      age: false,
      loginDialog: false,
      rules: {
        required: value => !!value || 'Required info.',
        counter: value => value.length <= 20 || 'Max 20 characters',
        password: v => v.length >= 7 || 'Min 7 characters',
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
          return pattern.test(value) || 'Invalid e-mail.'
        }
      }
    }
  },
  mounted () {
    this.init()
  },
  computed: {
    ...mapGetters({
      likedItems: 'getLikedItems',
      cartItems: 'getCartItems'
    }),
    // guard nav items according to user login status
    menuItems () {
      let menuItems = [
        {
          icon: 'local_bar',
          title: 'Find Your Drink',
          link: '/shop'
        },
        {
          icon: 'info',
          title: 'About Blk Party',
          link: '/about'
        }
      ]
      if (this.userAuthenticated) {
        menuItems = [
          {
            icon: 'local_bar',
            title: 'Find Your Drink',
            link: '/shop'
          },
          {
            icon: 'favorite',
            title: 'Wishlists',
            link: '/favorite'
          },
          {
            icon: 'account_circle',
            title: 'Profile',
            link: '/profile'
          }
        ]
      }
      if (this.vendorAuthenticated) {
        menuItems = [
          {
            icon: 'insert_chart',
            title: 'Dashboard',
            link: '/dashboard'
          },
          {
            icon: 'local_bar',
            title: 'Find Your Drink',
            link: '/shop'
          },
          {
            icon: 'favorite',
            title: 'Wishlists',
            link: '/favorite'
          }
        ]
      }
      return menuItems
    },
    userAuthenticated () {
      return this.$store.getters.getUser !== null && this.$store.getters.getUser !== undefined
    },
    user () {
      return this.$store.getters.getUser
    },
    vendorAuthenticated () {
      return this.$store.getters.getVendor !== null && this.$store.getters.getVendor !== undefined
    },
    vendor () {
      return this.$store.getters.getVendor
    },
    loggedIn () {
      if (this.userAuthenticated) {
        return true
      } else if (this.vendorAuthenticated) {
        return true
      } else {
        return false
      }
    },
    buyer () {
      if (this.userAuthenticated) {
        return this.user
      } else if (this.vendorAuthenticated) {
        return this.vendor
      } else {
        return null
      }
    },
    cartCount () {
      let itemsCount = 0
      if (this.cartItems === null || undefined) {
        return itemsCount
      } else {
        itemsCount = this.cartItems.length
        return itemsCount
      }
    },
    error () {
      return this.$store.getters.getError
    },
    loading () {
      return this.$store.getters.getLoading
    }
  },
  // watch for user/vendor login state and redirect
  watch: {
    // listen to user login states
    buyer (value) {
      if (
        value.type === 'user'
      ) {
        this.$router.push('/shop')
        this.loginDialog = true
      } else if (
        value.type === 'vendor'
      ) {
        this.$router.push('/dashboard')
        this.loginDialog = true
      } else {
        this.$router.push('/')
      }
    },
    // create search by query function and export to $root instance
    query (value) {
      this.$router.push('/shop')
      this.$root.$emit('searchQuery', value)
    }
  },
  methods: {
    // initialization functions to be called on mounted lifecycle hook
    init () {
      if (this.loggedIn) {
        this.$store.dispatch('likedItemsApi', {userId: this.buyer.id})
        this.$store.dispatch('cartItemsApi', {userId: this.buyer.id})
      }
    },
    changeRoute (a) {
      this.$router.push({ path: a.route })
    },
    onLogout () {
      this.$store.dispatch('logout')
    },
    onDismissed () {
      this.$store.dispatch('clearSuccess')
      this.$store.dispatch('clearError')
    },
    // user login/register methods
    switchUserLogin () {
      this.userLogin = true
      this.userRegister = false
      this.vendorLogin = false
      this.vendorRegister = false
      this.loginStatus = 'Login'
    },
    switchUserRegister () {
      this.userLogin = false
      this.userRegister = true
      this.vendorLogin = false
      this.vendorRegister = false
      this.loginStatus = 'Register'
    },
    switchVendorLogin () {
      this.userLogin = false
      this.userRegister = false
      this.vendorLogin = true
      this.vendorRegister = false
      this.loginStatus = 'Login'
    },
    switchVendorRegister () {
      this.userLogin = false
      this.userRegister = false
      this.vendorLogin = false
      this.vendorRegister = true
      this.loginStatus = 'Register'
    },
    onUserLogin () {
      if (!this.age) {
        this.$store.dispatch('setError', {message: 'Please declare if you are 18 year-old and above.'})
      } else {
        this.$store.dispatch('userLogin', {
          email: this.email,
          password: this.password
        })
        this.onDismissed()
      }
    },
    onUserRegister () {
      if (!this.age) {
        this.$store.dispatch('setError', {message: 'Please declare if you are 18 year-old and above.'})
      } else if (this.password === this.retypePassword) {
        this.$store.dispatch('clearError')
        this.$store.dispatch('userRegister', {
          email: this.email,
          password: this.password,
          name: this.name,
          avatar: this.avatar,
          dateJoined: new Date()
        })
        this.onDismissed()
      } else {
        this.$store.dispatch('setError', {message: 'Passwords do not match. Please retype password.'})
      }
    },
    onVendorLogin () {
      if (!this.age) {
        this.$store.dispatch('setError', {message: 'Please declare if you are 18 year-old and above.'})
      } else {
        this.$store.dispatch('vendorLogin', {
          email: this.email,
          password: this.password
        })
        this.onDismissed()
      }
    },
    onVendorRegister () {
      if (!this.age) {
        this.$store.dispatch('setError', {message: 'Please declare if you are 18 year-old and above.'})
      } else if (this.password === this.retypePassword) {
        this.$store.dispatch('clearError')
        this.$store.dispatch('vendorRegister', {
          email: this.email,
          password: this.password,
          name: this.name,
          avatar: this.avatar,
          dateJoined: new Date()
        })
        this.onDismissed()
      } else {
        this.$store.dispatch('setError', {message: 'Passwords do not match. Please retype password.'})
      }
    },
    illegalAge () {
      this.$router.push('/')
      this.$store.dispatch('logout')
      this.$store.dispatch('setSuccess', {message: 'Thank you for your honesty. Please come back when you are of legal drinking age.'})
      this.loginDialog = false
    },
    legalAge () {
      this.loginDialog = false
    }
  },
  name: 'App'
}
</script>

<style scoped>
  .search {
    height: 50px;
    width: 100%;
    top: -20px;
  }
  .custom-loader {
    animation: loader 1s infinite;
    display: flex;
  }
  @-moz-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-o-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
</style>




