import Vue from 'vue'
import Vuex from 'vuex'

import mutations from './mutations'
import actions from './actions'
import getters from './getters'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    invoices: ['Loading...'],
    items: [],
    orders: [],
    likedItems: [],
    cartItems: [],
    sales: [{valueSGD: 'Loading...'}],
    labels: [{date: 'Loading...'}],
    overall: [
      {
        valueSGD: '0',
        change: '0',
        month: 'Dec'
      }],
    vendor: null,
    user: null,
    users: null,
    error: null,
    success: null,
    loading: false
  },
  mutations,
  actions,
  getters
})



