import * as firebase from 'firebase'

export default {
  // action to get all user details
  allUsersApi ({commit}) {
    commit('SET_LOADING', true)
    firebase.database().ref('users').once('value')
    .then(data => {
      const users = []
      const obj = data.val()
      for (let key in obj) {
        users.push({
          userId: key,
          userName: obj[key].profile.name,
          userEmail: obj[key].profile.email,
          userType: obj[key].profile.type
        })
      }
      commit('SET_USERS', users)
      commit('SET_LOADING', false)
    })
    .catch(error => {
      commit('SET_ERROR', error.message)
      commit('SET_LOADING', false)
    })
  },
  // action to get all items
  itemsApi ({commit}) {
    commit('SET_LOADING', true)
    firebase.database().ref('items').child('booze').once('value')
    .then(data => {
      const items = []
      const obj = data.val()
      for (let key in obj) {
        items.push({
          itemId: obj[key].itemId,
          itemName: obj[key].itemName,
          description: obj[key].description,
          priceSGD: obj[key].priceSGD,
          discount: obj[key].discount,
          discountAmt: obj[key].discountAmt,
          discountedPrice: obj[key].discountedPrice,
          vendorId: obj[key].vendorId,
          createDate: obj[key].createDate,
          deliveryTimeDays: obj[key].deliveryTimeDays,
          deliveryTimeHrs: obj[key].deliveryTimeHrs,
          itemType: obj[key].itemType,
          imageURL: obj[key].imageURL
        })
      }
      commit('SET_ITEMS', items)
      commit('SET_LOADING', false)
    })
    .catch(error => {
      commit('SET_ERROR', error.message)
      commit('SET_LOADING', false)
    })
  },
  // action to get all items
  ordersApi ({commit}, payload) {
    commit('SET_LOADING', true)
    firebase.database().ref('users').child(payload.userId).child('orders').once('value')
    .then(data => {
      const orders = []
      const obj = data.val()
      for (let key in obj) {
        orders.push({
          itemId: obj[key].itemId,
          itemName: obj[key].itemName,
          dateOrdered: obj[key].dateOrdered,
          timeOrdered: obj[key].timeOrdered,
          quantity: obj[key].quantity
        })
      }
      commit('SET_ORDERS', orders)
      commit('SET_LOADING', false)
    })
    .catch(error => {
      commit('SET_ERROR', error.message)
      commit('SET_LOADING', false)
    })
  },
  // action to enter vendor orders
  sendOrderApi ({commit}, payload) {
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')
    commit('CLEAR_SUCCESS')
    const vendorId = payload.vendorId
    const userId = payload.userId
    const order = {
      itemId: payload.itemId,
      itemName: payload.itemName,
      vendorId: vendorId,
      vendorName: payload.vendorName,
      vendorEmail: payload.vendorEmail,
      userId: userId,
      deliveryEmail: payload.deliveryEmail,
      firstName: payload.firstName,
      lastName: payload.lastName,
      fee: payload.fee,
      discountAmt: payload.discountAmt,
      destination: payload.destination,
      zipCode: payload.zipCode,
      phoneNumber: payload.phoneNumber,
      dateOrdered: payload.dateOrdered.toLocaleDateString(),
      timeOrdered: payload.dateOrdered.toLocaleTimeString(),
      monthOrdered: payload.dateOrdered.getMonth() + 1,
      expectedDate: payload.expectedDelivery.toLocaleDateString(),
      expectedTime: payload.expectedDelivery.toLocaleTimeString(),
      dateDelivered: payload.dateDelivered,
      timeDelivered:
      payload.dateDelivered,
      priceSGD: payload.priceSGD,
      amountPaid: payload.amountPaid,
      quantity: payload.quantity,
      netSales: payload.netSales
    }
    let key
    // create order key first in vendor's db field
    firebase.database().ref('users').child(vendorId).child('invoices').push(order)
    .then((data) => {
      key = data.key
      // update order with orderId
      order.orderId = key
      return key
    })
    // retrieve key to store as file property in firebase DB
    .then(key => {
      // update vendor field with invoiceId
      firebase.database().ref('users').child(vendorId).child('invoices').child(key).update({
        invoiceId: key
      })
      // update user field with order
      firebase.database().ref('users').child(userId).child('orders').child(key).update(order)
      // stop loading and send success msg
      commit('SET_LOADING', false)
    })
    // store error message
    .catch(error => {
      commit('SET_ERROR', error)
      commit('SET_LOADING', false)
    })
  },
  // action to get vendor invoices
  vendorInvoicesApi ({commit}, payload) {
    commit('SET_LOADING', true)
    const vendorId = payload.id
    firebase.database().ref('users').child(vendorId).child('invoices').once('value')
    .then(data => {
      const invoices = []
      const obj = data.val()
      for (let key in obj) {
        invoices.push({
          invoiceId: obj[key].invoiceId,
          itemId: obj[key].itemId,
          itemName: obj[key].itemName,
          vendorId: obj[key].vendorId,
          userId: obj[key].userId,
          deliveryEmail: obj[key].deliveryEmail,
          firstName: obj[key].firstName,
          lastName: obj[key].lastName,
          fee: obj[key].fee,
          discountAmt: obj[key].discountAmt,
          destination: obj[key].destination,
          zipCode: obj[key].zipCode,
          phoneNumber: obj[key].phoneNumber,
          dateOrdered: obj[key].dateOrdered,
          timeOrdered: obj[key].timeOrdered,
          expectedDate: obj[key].expectedDate,
          expectedTime: obj[key].expectedTime,
          dateDelivered: obj[key].dateDelivered,
          timeDelivered: obj[key].timeDelivered,
          priceSGD: obj[key].priceSGD,
          amountPaid: obj[key].amountPaid,
          quantity: obj[key].quantity,
          netSales: obj[key].netSales
        })
      }
      commit('SET_INVOICES', invoices)
      commit('SET_LOADING', false)
    })
    .catch(error => {
      commit('SET_ERROR', error)
      commit('SET_LOADING', false)
    })
  },
  // action to get performance chart data
  chartApi ({commit}, payload) {
    commit('SET_LOADING', true)
    const vendorId = payload.id
    firebase.database().ref('users').child(vendorId).child('invoices').orderByKey().once('value')
    .then(d => {
      // set up chart params
      const chartData = []
      const chartLabels = []
      const months = []
      const obj = d.val()
      let prevDate
      let daySales
      // if no invoice, set date to today and sales to zero
      if (obj === null) {
        chartLabels.push(new Date().toLocaleDateString())
        months.push(new Date().getMonth())
        chartData.push(0)
      } else {
        // sum daily sales push sales and label data into separate arrays
        for (let key in obj) {
          if (obj[key].dateOrdered !== prevDate) {
            chartLabels.push(obj[key].dateOrdered
            )
            months.push(obj[key].monthOrdered)
            daySales = Number(obj[key].netSales)
            chartData.push(daySales)
          } else {
            // increment current day's sales by new order netSales
            chartData[chartData.length - 1] = daySales + Number(obj[key].netSales)
            // store new daily sales in dailySales var
            daySales = chartData[chartData.length - 1]
          }
          prevDate = obj[key].dateOrdered
        }
      }
      // get overall sales performance
      // first, combine chartData and chartLabels
      let dailySales = chartLabels.map((value, index) => ({
        date: value,
        month: months[index],
        valueSGD: chartData[index]
      }))
      // set monthly sales params
      let sales = []
      let monthsOverall = []
      let prevMonth
      let monthlySales
      // convert month number to month name
      let monthNames = [
        'January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'
      ]
      for (let i = 0; i < dailySales.length; i++) {
        // check if each element's month equals current month
        if (dailySales[i].month !== prevMonth) {
          // convert month number to month name
          monthsOverall.push(monthNames[dailySales[i].month - 1])
          monthlySales = dailySales[i].valueSGD
          sales.push(monthlySales)
        } else {
          // increment current month's sales by new daily sales
          sales[sales.length - 1] = monthlySales + dailySales[i].valueSGD
          // store new daily sales in dailySales var
          monthlySales = sales[sales.length - 1]
        }
        prevMonth = dailySales[i].month
      }
      // set up monthly change array with first month change being zero
      let change = [0]
      if (sales.length > 1) {
        for (let i = 0; i < sales.length; i++) {
          change.push(
            ((sales[i] / sales[i - 1]) - 1) * 100
          )
        }
      }
      // set up overall array
      let overall = sales.map((value, index) => ({
        month: monthsOverall[index],
        valueSGD: value,
        change: change[index]
      }))
      // commit sales and labels to store
      commit('SET_SALES', chartData)
      commit('SET_LABELS', chartLabels)
      commit('SET_OVERALL', overall)
      // turn off loading
      commit('SET_LOADING', false)
    })
    // store error, if any
    .catch(error => {
      commit('SET_ERROR', error.message)
      commit('SET_LOADING', false)
    })
  },
  // user login actions
  userLogin ({commit}, payload) {
    commit('CLEAR_ERROR')
    commit('SET_LOADING', true)
    firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
    .then(data => {
      const userId = data.user.uid
      firebase.database().ref('users').child(userId).child('profile').once('value')
      .then(profile => {
        const obj = profile.val()
        if (obj.type === 'user') {
          const newUser = {
            id: userId,
            email: payload.email,
            name: obj.name,
            dateJoined: obj.dateJoined,
            avatar: obj.avatar,
            type: obj.type
          }
          commit('SET_USER', newUser)
          commit('SET_VENDOR', null)
          commit('SET_LOADING', false)
        } else {
          commit('SET_VENDOR', null)
          commit('SET_LOADING', false)
          commit('SET_ERROR', 'User profile missing. Please login as vendor by clicking above or register as a new user.')
        }
      })
    })
    .catch(error => {
      commit('SET_LOADING', false)
      commit('SET_ERROR', error.message)
    })
  },
  // vendor login actions
  vendorLogin ({commit}, payload) {
    commit('CLEAR_ERROR')
    commit('SET_LOADING', true)
    firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
    .then(data => {
      const vendorId = data.user.uid
      firebase.database().ref('users').child(vendorId).child('profile').once('value')
      .then(profile => {
        const obj = profile.val()
        if (obj.type === 'vendor') {
          const newVendor = {
            id: vendorId,
            email: payload.email,
            name: obj.name,
            dateJoined: obj.dateJoined,
            avatar: obj.avatar,
            type: obj.type
          }
          commit('SET_VENDOR', newVendor)
          commit('SET_USER', null)
          commit('SET_LOADING', false)
        } else {
          commit('SET_USER', null)
          commit('SET_LOADING', false)
          commit('SET_ERROR', 'Vendor profile missing. Please login as user by clicking above or register as a new vendor.')
        }
      })
    })
    .catch(error => {
      commit('SET_LOADING', false)
      commit('SET_ERROR', error.message)
    })
  },
  // auto login functions
  autoUserLogin ({commit}, payload) {
    const loginId = payload.uid
    firebase.database().ref('users').child(loginId).child('profile').once('value')
    .then(profile => {
      const obj = profile.val()
      if (obj.type === 'user') {
        const user = {
          id: loginId,
          email: payload.email,
          name: obj.name,
          dateJoined: obj.dateJoined,
          avatar: obj.avatar,
          type: obj.type
        }
        commit('SET_USER', user)
        commit('SET_LOADING', false)
      } else {
        const vendor = {
          id: loginId,
          email: payload.email,
          name: obj.name,
          dateJoined: obj.dateJoined,
          avatar: obj.avatar,
          type: obj.type
        }
        commit('SET_VENDOR', vendor)
        commit('SET_LOADING', false)
      }
    })
    .catch(error => {
      commit('SET_LOADING', false)
      commit('SET_ERROR', error.message)
    })
  },
  // user register actions
  userRegister ({commit}, payload) {
    commit('CLEAR_ERROR')
    commit('SET_LOADING', true)
    firebase.auth().createUserWithEmailAndPassword(payload.email, payload.password)
    .then(data => {
      const newUser = {
        email: payload.email,
        name: payload.name,
        avatar: payload.avatar,
        dateJoined: payload.dateJoined.toISOString(),
        type: 'user'
      }
      const userId = data.user.uid
      // create vendor child with userId in firebase DB
      return firebase.database().ref('users').child(userId).child('profile').set(newUser)
      .then(() => {
        commit('SET_USER', {
          ...newUser,
          id: userId
        })
        commit('SET_LOADING', false)
      })
      .catch(error => {
        commit('SET_LOADING', false)
        commit('SET_ERROR', error.message)
      })
    })
    .catch(error => {
      commit('SET_LOADING', false)
      commit('SET_ERROR', error.message)
    })
  },
  // vendor register actions
  vendorRegister ({commit}, payload) {
    commit('CLEAR_ERROR')
    commit('SET_LOADING', true)
    firebase.auth().createUserWithEmailAndPassword(payload.email, payload.password)
    .then(data => {
      const newVendor = {
        email: payload.email,
        name: payload.name,
        avatar: payload.avatar,
        dateJoined: payload.dateJoined.toISOString(),
        type: 'vendor'
      }
      const vendorId = data.user.uid
      // create vendor child with userId in firebase DB
      return firebase.database().ref('users').child(vendorId).child('profile').set(newVendor)
      .then(() => {
        commit('SET_VENDOR', {
          ...newVendor,
          id: vendorId
        })
        commit('SET_LOADING', false)
      })
    })
    .catch(error => {
      commit('SET_LOADING', false)
      commit('SET_ERROR', error.message)
    })
  },
  // action to upload files
  uploadFile ({commit}, payload) {
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')
    commit('CLEAR_SUCCESS')
    const file = {
      itemName: payload.itemName,
      priceSGD: payload.priceSGD,
      description: payload.description,
      itemType: payload.itemType,
      discount: payload.discount,
      discountAmt: payload.discountAmt,
      discountedPrice: payload.discountedPrice,
      deliveryTimeDays: payload.deliveryTimeDays,
      deliveryTimeHrs: payload.deliveryTimeHrs,
      vendorId: payload.vendorId,
      createDate: payload.createDate.toISOString()
    }
    let key
    // create file key first in firebase DB
    firebase.database().ref('items').child('booze').push(file)
    .then((data) => {
      key = data.key
      return key
    })
    // store image in firebase storage
    .then(key => {
      return firebase.storage().ref('files/' + 'f' + key).put(payload.file)
    })
    // retrieve image URL to store as file property in firebase DB
    .then(fileData => {
      fileData.ref.getDownloadURL()
      .then(url => {
        return firebase.database().ref('items').child('booze').child(key).update({
          imageURL: url,
          itemId: key
        })
      })
      commit('SET_LOADING', false)
      commit('SET_SUCCESS', 'All item(s) uploaded!')
    })
    // store error message
    .catch(error => {
      commit('SET_ERROR', error)
      commit('SET_LOADING', false)
    })
  },
  // action to update selected file
  updateFile ({commit}, payload) {
    const item = {
      itemId: payload.itemId,
      itemName: payload.itemName,
      priceSGD: payload.priceSGD,
      description: payload.description,
      itemType: payload.itemType,
      discount: payload.discount,
      discountAmt: payload.discountAmt,
      discountedPrice: payload.discountedPrice,
      deliveryTimeDays: payload.deliveryTimeDays,
      deliveryTimeHrs: payload.deliveryTimeHrs,
      vendorId: payload.vendorId,
      createDate: payload.createDate,
      updateDate: payload.updateDate.toISOString()
    }
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')
    commit('CLEAR_SUCCESS')
    let itemId = payload.itemId
    // find by itemId and update item
    firebase.database().ref('items').child('booze').child(itemId).set(item)
    // if no updatedFile, no change to imageURL
    if (payload.updatedFile === null) {
      firebase.database().ref('items').child('booze').child(itemId).update({
        imageURL: payload.imageURL
      })
      .then(() => {
        commit('SET_LOADING', false)
        commit('SET_SUCCESS', 'Item updated!')
      })
      // store error message
      .catch(error => {
        commit('SET_ERROR', error)
        commit('SET_LOADING', false)
      })
    } else {
      // store new image in firebase storage
      firebase.storage().ref('files/' + 'f' + itemId).put(payload.updatedFile)
      // retrieve image URL and update item imageURL
      .then(fileData => {
        fileData.ref.getDownloadURL()
        .then(url => {
          return firebase.database().ref('items').child('booze').child(itemId).update({
            imageURL: url
          })
        })
        commit('SET_LOADING', false)
        commit('SET_SUCCESS', 'Item updated!')
      })
      // store error message
      .catch(error => {
        commit('SET_ERROR', error)
        commit('SET_LOADING', false)
      })
    }
  },
  // action to delete one item
  deleteItem ({commit}, payload) {
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')
    // remove item from firebase
    firebase.database().ref('items').child('booze').child(payload.itemId).remove()
    .then(() => {
      commit('SET_LOADING', false)
      commit('SET_SUCCESS', 'Item successfully deleted')
    })
    .catch(error => {
      commit('SET_ERROR', error)
      commit('SET_LOADING', false)
    })
  },
  // action to like one item
  likeItem ({commit}, payload) {
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')
    // remove item from firebase
    firebase.database().ref('users').child(payload.userId).child('liked').child(payload.itemId).set({
      itemId: payload.itemId,
      likedList: payload.likedList
    })
    .then(() => {
      commit('SET_LOADING', false)
      commit('SET_SUCCESS', 'Item added to wishlist!')
    })
    .catch(error => {
      commit('SET_ERROR', error)
      commit('SET_LOADING', false)
    })
  },
  // action to unlike one item
  unlikeItem ({commit}, payload) {
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')
    // remove item from firebase
    firebase.database().ref('users').child(payload.userId).child('liked').child(payload.itemId).remove()
    .then(() => {
      commit('SET_LOADING', false)
      commit('SET_SUCCESS', 'Item removed from wishlist.')
    })
    .catch(error => {
      commit('SET_ERROR', error)
      commit('SET_LOADING', false)
    })
  },
  // action to get all liked items
  likedItemsApi ({commit}, payload) {
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')
    firebase.database().ref('users').child(payload.userId).child('liked').once('value')
    .then(data => {
      const likedItems = []
      const obj = data.val()
      for (let key in obj) {
        likedItems.push({
          itemId: obj[key].itemId,
          likedList: obj[key].likedList
        })
      }
      commit('SET_LIKEDITEMS', likedItems)
      commit('SET_LOADING', false)
    })
    .catch(error => {
      commit('SET_ERROR', error.message)
      commit('SET_LOADING', false)
    })
  },
  // action to add one item to cart
  addItemToCart ({commit}, payload) {
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')
    // remove item from firebase
    firebase.database().ref('users').child(payload.userId).child('cart').child(payload.itemId
    ).set({
      itemId: payload.itemId,
      qty: payload.qty
    })
    .then(() => {
      commit('SET_LOADING', false)
      commit('SET_SUCCESS', 'Item added to shopping cart!')
    })
    .catch(error => {
      commit('SET_ERROR', error)
      commit('SET_LOADING', false)
    })
  },
  // action to update one item in cart
  updateItemInCart ({commit}, payload) {
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')
    // remove item from firebase
    firebase.database().ref('users').child(payload.userId).child('cart').child(payload.itemId
    ).set({
      itemId: payload.itemId,
      qty: payload.qty
    })
    .then(() => {
      commit('SET_LOADING', false)
    })
    .catch(error => {
      commit('SET_ERROR', error)
      commit('SET_LOADING', false)
    })
  },
  // action to remove one item from cart
  removeItemFromCart ({commit}, payload) {
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')
    // remove item from firebase
    firebase.database().ref('users').child(payload.userId).child('cart').child(payload.itemId).remove()
    .then(() => {
      commit('SET_LOADING', false)
      commit('SET_SUCCESS', payload.message)
    })
    .catch(error => {
      commit('SET_ERROR', error)
      commit('SET_LOADING', false)
    })
  },
  // action to get all cart items
  cartItemsApi ({commit}, payload) {
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')
    firebase.database().ref('users').child(payload.userId).child('cart').once('value')
    .then(data => {
      const cartItems = []
      const obj = data.val()
      for (let key in obj) {
        cartItems.push({
          itemId: obj[key].itemId,
          qty: obj[key].qty
        })
      }
      commit('SET_CARTITEMS', cartItems)
      commit('SET_LOADING', false)
    })
    .catch(error => {
      commit('SET_ERROR', error.message)
      commit('SET_LOADING', false)
    })
  },
  logout ({commit}) {
    firebase.auth().signOut()
    commit('SET_USER', null)
    commit('SET_VENDOR', null)
    commit('SET_INVOICES', null)
    commit('SET_OVERALL', null)
    commit('SET_LIKEDITEMS', null)
    commit('SET_CARTITEMS', null)
    commit('SET_ITEMS', null)
    commit('SET_SALES', null)
    commit('SET_LABELS', null)
    commit('CLEAR_ERROR')
    commit('CLEAR_SUCCESS')
  },
  setSuccess ({commit}, payload) {
    commit('SET_SUCCESS', payload.message)
  },
  setError ({commit}, payload) {
    commit('SET_ERROR', payload.message)
  },
  clearSuccess ({commit}) {
    commit('CLEAR_SUCCESS')
  },
  clearError ({commit}) {
    commit('CLEAR_ERROR')
  }
}



