<template>
  <v-container grid-list-md fluid>
 
    <!-- success alert -->
    <div
    class="mt-4"
    v-if="success">
      <app-success-alert 
      @dismissed="onDismissed"
      :text="success"
      class="white--text"></app-success-alert>
    </div>

    <!-- error alert -->
    <div
    class="mt-4"
    v-if="error">
      <app-error-alert 
      @dismissed="onDismissed"
      :text="error"
      class="black--text"></app-error-alert>
    </div>

    <!-- item list -->
    <v-layout 
    row wrap 
    v-if="loading">
      <v-flex xs12 class="text-xs-center mt-5">
        Getting your wishlists...
        <v-progress-circular 
        indeterminate 
        color="green"
        :width="3"
        :size="20"
        ></v-progress-circular>
      </v-flex>
    </v-layout>
    <v-layout 
    row wrap
    v-if="!listsLoaded">
      <v-flex xs12 class="text-xs-center mt-5">
        <h3>No wishlists to display yet.</h3>
      </v-flex>
    </v-layout>
    <v-layout 
    row wrap 
    v-if="listsLoaded"
    class="mt-3">
      <h3 class="headline mb-3">
        Your Wishlist(s)
      </h3>
    </v-layout>
    <v-layout
    row wrap
    v-if="listsLoaded">
      <v-flex
        v-for="(list, i) in lists"
        :key="i"
        xs12 sm4
      >
        <v-hover>
          <v-card
          slot-scope="{ hover }"
          :class="`elevation-${hover ? 12 : 2}`"
          height="280px">
            <v-img
              :src="list.items[0].imageURL"
              height="180px"
              contain
              style="cursor: pointer"
              @click="openDialog(list)"
            >
            </v-img>

            <v-card-title primary-title>
              <div>
                <div class="headline">{{list.name}}</div>
                <div>{{list.count}} item(s)</div>
              </div>
            </v-card-title>
          </v-card>
        </v-hover>
      </v-flex>
    </v-layout>

    <!-- list dialog -->
    <v-dialog
    transition="dialog-bottom-transition"
    width="600px"
    max-height="600px"
    scrollable
    v-model="listDialog">
      <v-card
      width="500px">
        <v-btn 
        icon 
        @click="listDialog = false">
          <v-icon>close</v-icon>
        </v-btn>
        <v-card-title>
          <span class="headline ml-3">{{listName}}</span>
        </v-card-title>
        <v-card-text>
          <div>
            <v-list
            two-line>
              <v-list-tile
                v-for="(listItem, key) in listItems"
                :key="key"
                avatar
                class="my-3"
              >
                <v-list-tile-avatar>
                  <img :src="listItem.imageURL">
                </v-list-tile-avatar>
                <v-layout row wrap>

                  <v-flex xs12 sm9>
                    <v-list-tile-content v-if="listItem.discount === '0'">
                      <v-list-tile-title class="title">
                        {{listItem.itemName}}
                      </v-list-tile-title>
                      <v-list-tile-sub-title
                      class="mt-2">
                        SGD {{listItem.priceSGD}} each
                      </v-list-tile-sub-title>
                    </v-list-tile-content>
                    <v-list-tile-content
                    v-if="listItem.discount !== '0'">
                      <v-list-tile-title class="title">
                        {{listItem.itemName}}
                      </v-list-tile-title>
                      <v-list-tile-sub-title
                      class="mt-2">
                        SGD <s>{{listItem.priceSGD}}</s> {{listItem.discountedPrice}} each
                        <span class="success--text font-weight-bold">{{listItem.discount}}% discount!</span>
                      </v-list-tile-sub-title>
                    </v-list-tile-content>
                  </v-flex>

                  <v-flex xs12 sm3>
                    <v-list-tile-action
                    justify-space-around
                    class="mt-1">
                      <!-- unlike button -->
                      <v-layout row wrap>
                        <v-tooltip
                        bottom
                        class="mx-2"
                        >
                          <v-btn 
                          slot="activator"
                          icon
                          @click="unlike(listItem.itemId)">
                            <v-icon 
                            color="error">favorite</v-icon>
                          </v-btn>
                          <span>Remove from Wishlist</span>
                        </v-tooltip>
                        <!-- shopping cart buttons -->
                        <v-tooltip
                        bottom>
                          <v-menu
                          :close-on-content-click="false"
                          v-if="!addedToCart(listItem.itemId)"
                          slot="activator"
                          >
                            <v-btn
                            slot="activator"
                            @click="setItemQty(1)"
                            icon>
                              <v-icon color="success">add_shopping_cart</v-icon>
                            </v-btn>
                            <v-card>
                              <v-card-text class="pa-4">
                                <v-text-field
                                  color="success"
                                  v-model="qty"
                                  outline
                                  single-line
                                  type="number"
                                  :rules="rules.qty"
                                  suffix="unit(s)"
                                >
                                  <v-tooltip
                                    slot="append-outer"
                                    style="top: -12px"
                                    bottom
                                  >
                                    <v-btn
                                    :disabled="!qtyValid"      
                                    slot="activator"
                                    flat
                                    icon
                                    @click="addToCart(listItem.itemId)">
                                      <v-icon
                                      color="success">
                                        check
                                      </v-icon>
                                    </v-btn>
                                    Submit Order
                                  </v-tooltip>
                                </v-text-field>
                              </v-card-text>
                            </v-card>
                          </v-menu>
                          <span>Add to Cart</span>
                        </v-tooltip>
                        <v-tooltip
                        bottom>
                          <v-menu
                          :close-on-content-click="false"
                          v-if="addedToCart(listItem.itemId)"
                          slot="activator"
                          >
                            <v-btn
                            slot="activator"
                            @click="setItemQty(listItem.qty)"
                            icon>
                              <v-icon color="success">shopping_cart</v-icon>
                            </v-btn>
                            <v-card>
                              <v-card-text class="pa-4">
                                <v-text-field
                                  color="success"
                                  v-model="qty"
                                  outline
                                  single-line
                                  type="number"
                                  :rules="rules.qty"
                                  suffix="unit(s)"
                                >
                                  <v-tooltip
                                    slot="append-outer"
                                    style="top: -12px"
                                    bottom
                                  >
                                    <v-btn
                                    :disabled="!qtyValid"
                                    slot="activator"
                                    flat
                                    icon
                                    @click="addToCart(listItem.itemId)">
                                      <v-icon
                                      color="success">
                                        check
                                      </v-icon>
                                    </v-btn>
                                    Submit Order
                                  </v-tooltip>
                                  <v-tooltip
                                    slot="append-outer"
                                    style="top: -12px"
                                    bottom
                                  >
                                    <v-btn
                                    slot="activator"
                                    flat
                                    icon
                                    @click="removeFromCart(listItem.itemId)">
                                      <v-icon
                                      color="error">
                                        clear
                                      </v-icon>
                                    </v-btn>
                                    Remove All
                                  </v-tooltip>
                                </v-text-field>
                              </v-card-text>
                            </v-card>
                          </v-menu>
                          <span>Adjust Item Qty</span>
                        </v-tooltip>
                        <p
                        v-if="addedToCart(listItem.itemId)"
                        >
                          x {{listItem.qty}}
                        </p>
                      </v-layout>
                    </v-list-tile-action>
                  </v-flex>
                </v-layout>

              </v-list-tile>
            </v-list>
          </div>
        </v-card-text>

      </v-card>
    </v-dialog>

  </v-container>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      buyerId: null,
      listDialog: false,
      listName: '',
      listItems: [],
      qty: 1,
      rules: {
        qty: [val => val > 0 || 'Enter 1 or more in quantity please.']
      }
    }
  },
  mounted () {
    this.init()
  },
  computed: {
    ...mapGetters({
      items: 'getUpdatedItems',
      likedItems: 'getLikedItems',
      cartItems: 'getCartItems'
    }),
    user () {
      return this.$store.getters.getUser
    },
    vendor () {
      return this.$store.getters.getVendor
    },
    lists () {
      // set params
      let listNames = []
      let listCount = []
      let allItems = []
      let listItems = []
      let results = []
      let prev = ''

      // set function to get all liked lists
      function getLikedListName (item) {
        let likedList = item.likedList
        return likedList
      }
      // set function to get all liked item id
      function getLikedItemId (item) {
        let itemId = item.itemId
        return itemId
      }
      // get array of liked item ids
      let itemIdArr = this.likedItems.map(getLikedItemId)
      // push details of all liked items into allItems array
      for (let i = 0; i < this.items.length; i++) {
        let itemId = this.items[i].itemId
        if (itemIdArr.indexOf(itemId) > -1) {
          allItems.push(this.items[i])
        }
      }
      // sort both allItems and likedItems by itemId in ascending order
      this.likedItems.sort(function (a, b) {
        return a.itemId - b.itemId
      })
      allItems.sort(function (a, b) {
        return a.itemId - b.itemId
      })
      // get array of list names from sorted likedItems
      let likedListNames = this.likedItems.map(getLikedListName)
      // combine each element in sorted likedItems and allItems arrays into initArray
      let initArray = likedListNames.map((value, index) => ({
        name: value,
        item: allItems[index]
      }))
      // lowercase list names and sort initArray
      initArray.sort((a, b) => {
        let nameA = a.name.toLowerCase()
        let nameB = b.name.toLowerCase()
        if (nameA < nameB) {
          return -1 // sort string ascending
        } else if (nameA > nameB) {
          return 1 // sort string descending
        } else {
          return 0 // default return value (no sorting)
        }
      })
      // loop over sorted initArray to push unique list names into listNames array, their frequencies into listCount array, and their items into listItems array
      for (let j = 0; j < initArray.length; j++) {
        if (initArray[j].name !== prev) {
          listNames.push(initArray[j].name)
          listCount.push(1)
          let arr = []
          arr.push(initArray[j].item)
          listItems.push(arr)
        } else {
          listCount[listCount.length - 1]++
          listItems[listItems.length - 1].push(initArray[j].item)
        }
        prev = initArray[j].name
      }
      // merge each corresponding elements in arrays listNames, listCount and allItems into an object and set it to results array
      results = listNames.map((value, index) => ({
        name: value,
        count: listCount[index],
        items: listItems[index]
      }))

      return results
    },
    listsLoaded () {
      return this.lists
    },
    error () {
      return this.$store.getters.getError
    },
    success () {
      return this.$store.getters.getSuccess
    },
    loading () {
      return this.$store.getters.getLoading
    }
  },
  methods: {
    // initialization functions to be called on mounted lifecycle hook
    init () {
      // load all available items
      this.$store.dispatch('itemsApi')
      if (this.user) {
        this.buyerId = this.user.id
        // load all liked items
        this.$store.dispatch('likedItemsApi', {userId: this.buyerId})
        // load all cart items
        this.$store.dispatch('cartItemsApi', {userId: this.buyerId})
      } else if (this.vendor) {
        this.buyerId = this.vendor.id
        // load all liked items
        this.$store.dispatch('likedItemsApi', {userId: this.buyerId})
        // load all cart items
        this.$store.dispatch('cartItemsApi', {userId: this.buyerId})
      }
    },
    // dismiss alerts
    onDismissed () {
      this.$store.dispatch('clearError')
      this.$store.dispatch('clearSuccess')
    },
    openDialog (list) {
      // set dialog params
      this.listName = list.name
      this.listItems = list.items
      // open listDialog
      this.listDialog = true
    },
    addToCart (itemId) {
      if (this.buyerId) {
        let payload = {
          userId: this.buyerId,
          itemId: itemId,
          qty: this.qty
        }
        this.$store.dispatch('addItemToCart', payload)
        this.$store.dispatch('cartItemsApi', {userId: this.buyerId})
        this.getItemQty(payload)
        this.listDialog = false
      } else {
        this.$store.dispatch('setSuccess', {message: 'Please login and/or register to add item to your shopping cart!'})
      }
    },
    removeFromCart (itemId) {
      if (this.buyerId) {
        let payload = {
          userId: this.buyerId,
          itemId: itemId
        }
        this.$store.dispatch('removeItemFromCart', payload)
        this.$store.dispatch('cartItemsApi', {userId: this.buyerId})
      }
    },
    getItemQty (payload) {
      for (let i = 0; i < this.listItems.length; i++) {
        if (this.listItems[i].itemId === payload.itemId) {
          this.listItems[i].qty = payload.qty
          break
        }
      }
    },
    addedToCart (itemId) {
      if (this.cartItems === null || undefined) {
        return false
      } else {
        let added = false
        for (let i = 0; i < this.cartItems.length; i++) {
          this.getItemQty(this.cartItems[i])
          if (this.cartItems[i].itemId === itemId) {
            added = true
            break
          }
        }
        return added
      }
    },
    qtyValid () {
      if (this.qty > 0) {
        return true
      } else {
        return false
      }
    },
    setItemQty (itemQty) {
      this.qty = itemQty
    },
    unlike (itemId) {
      let payload = {
        userId: this.buyerId,
        itemId: itemId
      }
      this.$store.dispatch('unlikeItem', payload)
      this.$store.dispatch('likedItemsApi', {userId: this.buyerId})
      this.listDialog = false
    }
  }
}
</script>




